* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
  overflow: hidden;
}

body {
  color: #fff;
  background-color: #121212;
  flex-direction: column;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, sans-serif;
  display: flex;
}

.scanner-header {
  z-index: 100;
  background-color: #1a1a2e;
  border-bottom: 1px solid #333;
  flex-shrink: 0;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  display: flex;
}

.scanner-header h1 {
  color: #fff;
  flex: 1;
  margin: 0;
  font-size: 18px;
  font-weight: 600;
}

.back-btn, .settings-btn {
  color: #fff;
  cursor: pointer;
  background: #ffffff1a;
  border: none;
  border-radius: 8px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  font-size: 20px;
  text-decoration: none;
  transition: background-color .2s;
  display: flex;
}

.back-btn:hover, .settings-btn:hover {
  background: #fff3;
}

.rescan-btn {
  color: #000;
  cursor: pointer;
  background: #00c896;
  border: none;
  border-radius: 8px;
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 600;
  transition: background-color .2s;
}

.rescan-btn:hover {
  background: #00a67d;
}

.rescan-btn[hidden] {
  display: none;
}

.camera-container {
  background: #000;
  flex-direction: column;
  flex: 1;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: relative;
  overflow: hidden;
}

.camera-viewport {
  background: #000;
  border-radius: 0;
  width: 100%;
  max-width: 500px;
  height: 70vh;
  max-height: 400px;
  position: relative;
  overflow: hidden;
}

resize-observer, capture-photo {
  width: 100%;
  height: 100%;
  display: block;
}

capture-photo::part(video) {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.scan-frame {
  pointer-events: none;
  border: 2px solid #00c896cc;
  border-radius: 8px;
  width: 80%;
  max-width: 300px;
  height: 50%;
  max-height: 180px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.scan-frame:before {
  content: "";
  background: linear-gradient(90deg, #0000, #00c896, #0000);
  height: 2px;
  animation: 2s ease-in-out infinite scan-line;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
}

@keyframes scan-line {
  0%, 100% {
    opacity: .3;
  }

  50% {
    opacity: 1;
  }
}

.zoom-controls {
  background: #0009;
  border-radius: 24px;
  align-items: center;
  gap: 12px;
  padding: 8px 16px;
  display: flex;
  position: absolute;
  bottom: 16px;
  left: 50%;
  transform: translateX(-50%);
}

.zoom-controls button {
  color: #fff;
  cursor: pointer;
  background: #fff3;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  font-size: 20px;
  font-weight: bold;
  display: flex;
}

.zoom-controls button:hover {
  background: #ffffff4d;
}

.zoom-controls span {
  color: #fff;
  text-align: center;
  min-width: 32px;
  font-size: 14px;
}

.scan-instructions {
  color: #fff;
  text-align: center;
  background: #0009;
  border-radius: 20px;
  padding: 10px 20px;
  font-size: 14px;
  position: absolute;
  bottom: 70px;
  left: 50%;
  transform: translateX(-50%);
}

.toast-container {
  z-index: 1000;
  flex-direction: column;
  gap: 8px;
  display: flex;
  position: fixed;
  top: 80px;
  left: 50%;
  transform: translateX(-50%);
}

.toast {
  color: #fff;
  background: #333;
  border-radius: 8px;
  padding: 12px 20px;
  font-size: 14px;
  animation: .3s ease-out toast-in;
  box-shadow: 0 4px 12px #0000004d;
}

.toast.success {
  color: #000;
  background: #00c896;
}

.toast.error {
  background: #e74c3c;
}

@keyframes toast-in {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.modal {
  color: #fff;
  background: #1a1a2e;
  border: none;
  border-radius: 16px;
  width: 90%;
  max-width: 340px;
  padding: 0;
  box-shadow: 0 8px 32px #00000080;
}

.modal::backdrop {
  background: #000000b3;
}

.modal-content {
  padding: 20px;
}

.modal-header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.modal-header h2 {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
}

.close-btn {
  color: #fff;
  cursor: pointer;
  background: #ffffff1a;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  display: flex;
}

.close-btn:hover {
  background: #fff3;
}

.settings-form {
  flex-direction: column;
  gap: 16px;
  display: flex;
}

.setting-item {
  cursor: pointer;
  background: #ffffff0d;
  border-radius: 8px;
  align-items: center;
  gap: 12px;
  padding: 12px;
  display: flex;
}

.setting-item input[type="checkbox"] {
  accent-color: #00c896;
  width: 20px;
  height: 20px;
}

.setting-item span {
  font-size: 15px;
}

.product-form {
  flex-direction: column;
  gap: 16px;
  display: flex;
}

.form-group {
  flex-direction: column;
  gap: 6px;
  display: flex;
}

.form-group label {
  color: #aaa;
  font-size: 13px;
}

.form-group input {
  color: #fff;
  background: #0a0a0a;
  border: 1px solid #333;
  border-radius: 8px;
  width: 100%;
  padding: 12px;
  font-size: 16px;
}

.form-group input:focus {
  border-color: #00c896;
  outline: none;
}

.btn-primary {
  color: #000;
  cursor: pointer;
  background: #00c896;
  border: none;
  border-radius: 8px;
  width: 100%;
  padding: 14px;
  font-size: 16px;
  font-weight: 600;
  transition: background-color .2s;
}

.btn-primary:hover {
  background: #00a67d;
}

@media (width >= 600px) {
  .scan-frame {
    width: 320px;
    height: 180px;
  }

  .scanner-header h1 {
    font-size: 20px;
  }
}

